import React, { Component } from 'react';
import TablePlace from './TablePlace';
import request from 'request-promise';

// accessing APIs locally
// const API_URL_BASE = "http://localhost:3000/api/";

// accessing APIs from deployed server
const API_URL_BASE = "http://localhost:5000/api/"

class App extends Component {

    state = {
        range : {
            left:"",
            right:""
        },
       
        places:[
            
        ]
     }; 

     handle_add_range(data){
        if(data.success == true)
        {
            var temp = {};
            temp.action = data.action;
            temp.input = data.input;
            temp.output = data.output;
            this.setState(prevState=>({
                current_address:{
                address : "",
                latlng: ""
                },
                places:[...prevState.places,temp ]
            }));
        }
        else
        {
            this.setState({
                range:{
                left : "",
                right:""
                }
            });           
        }
    };

    handle_add_range_error ()
    {
        console.log("error in calling API");
    }

    handleAddRange = function() {
        var left = parseInt(this.state.range.left);
        var right = parseInt(this.state.range.right);
        const options = {
            url: API_URL_BASE+"add",
            method: 'POST',
            json: {left: left, right:right}
        }
        request(options).then(data=>this.handle_add_range(data)).catch(data=>this.handle_add_range_error(data));
    }

    handleRemoveRange = function() {
        var left = parseInt(this.state.range.left);
        var right = parseInt(this.state.range.right);
        const options = {
            url: API_URL_BASE+"remove",
            method: 'POST',
            json: {left: left, right:right}
        }
        request(options).then(data=>this.handle_add_range(data)).catch(data=>this.handle_add_range_error(data));
    }

    handleChange = event => {
        const {name, value} = event.target;
        
        var range = this.state.range;
        console.log("current range is ",range);
        range[name] = value;
        
        this.setState({
            range:range
        });
    }

    add_range = () => {        
        if( this.state.range.left !== "" && this.state.range.right !== "")
        {
            console.log("range is ", this.state.range);
            this.handleAddRange();
        }
    }

    remove_range = () => {        
        if( this.state.range.left !== "" && this.state.range.right !== "")
        {
            console.log("range is ", this.state.range);
            this.handleRemoveRange();
        }
    }

    reset = () => {
        
        const options = {
            url: API_URL_BASE+"reset",
            method: 'POST',
            json: {}
        }
        request(options).then({}).catch({});
        this.setState ({
           range:{
               left:"",
               right:""
           },
           places:[]
        });
    }
    
    render() {

        var warning_msg = "";
        if(this.state.range.left=="" || this.state.range.right =="")
        {
            warning_msg = <label className="warning_msg" name="warning_msg"> Enter left and right end of the range.</label>;
        }

        var tab_holder = "";

        tab_holder =
        <div className="TabHolder">            
                <br />
                <p>Operation Logs. Click on Reset to start-over</p>
                <TablePlace placeData={this.state.places} removePlace={this.removePlace} />
        </div>
        
        return (
            
            <div className="container">
                <div className="title_bar">
                  <label className="title_text">Range class UI </label>  
                </div>

                <div className="form_holder">
                <form>
                    <label className="my_label">left</label>
                    <input className="my_label"
                        type="text" 
                        name="left" 
                        value={this.state.range.left} 
                        onChange={this.handleChange} />
                    <label className="my_label" >right</label>
                    <input className="my_label"
                        type="text" 
                        name="right" 
                        value={this.state.range.right} 
                        onChange={this.handleChange}/>
                    {warning_msg}
                    <input 
                        type="button" 
                        value="Add" 
                        onClick={this.add_range} />
                    <input 
                        type="button" 
                        value="Remove"
                        style={{marginLeft:20}} 
                        onClick={this.remove_range} />
                    <input 
                        type="button" 
                        value="Reset"
                        style={{marginLeft:20}} 
                        onClick={this.reset} />
                </form>
                </div>

                {tab_holder}
            </div>
        );
    }
}

export default App;