import React, {Component} from 'react';

class TablePlace extends Component {
    render() {
        const { placeData, removePlace } = this.props;

        return (
            <table>
                <TableHeader />
                <TableBody placeData={placeData} removePlace={removePlace}/>
            </table>
        );
    }
}

const TableHeader = () => { 
    return (
        <thead>
            <tr>
                <th>index</th>
                <th>Action</th>
                <th>Range</th>
                <th>Output</th>
            </tr>
        </thead>
    );
}

const TableBody = props => { 
    const rows = props.placeData.map((row, index) => {
        return (
            <tr key={index}>
                <td>{index+1}</td>
                <td>{row.action}</td>
                <td>{row.input}</td>
                <td>{row.output}</td>
            </tr>
        );
    });

    return <tbody>{rows}</tbody>;
}

export default TablePlace;